export default {
  data() {
    return {
      loading: false,
      success: false,
      errored: false,
      isBot: false,
      name: "",
      bot: null,
      email: "",
      phone: "",
      message: "",
    };
  },
  methods: {
    sendMessage() {
      this.loading = true;
      /* This is where I check if the bot field has a value. 
         Only bots will fill this out. */
      if (this.bot != null) {
        this.isBot = true;
      }
      // Otherwise the form will try to go through.
      else {
        this.$http
          .post("https://admin.vetsbenefitsconsulting.com/messages", {
            name: this.name,
            email: this.email,
            phone: this.phone,
            message: this.message,
          })
          .then((response) => {
            this.success = true;
            this.errored = false;
          })
          .catch((error) => {
            this.errored = true;
          })
          .finally(() => {
            this.loading = false;
          });
      }
    },
  },
};
